<template lang="html">
  <div class="choose-radio">
    <div class="choose-radio-item borderB clearfix">
      <i class="pay-icon fl"></i>
      <span class="choose-radio-item__describe fl">支付宝支付</span>
      <!-- <mt-radio class="fr" @change="payMethods" title="单选框列表" v-model="value" :options="['zhi']"></mt-radio> -->
    </div>

    <!-- <div class="choose-radio-item borderB clearfix">
      <i class="pay-icon fl"></i>
      <span class="choose-radio-item__describe fl">微信支付</span>
      <mt-radio class="fr" @change="payMethods" title="单选框列表" v-model="value" :options="['wei']"></mt-radio>
    </div>

    <div class="choose-radio-item clearfix">
      <i class="pay-icon fl"></i>
      <span class="choose-radio-item__describe fl">银联支付</span>
      <span class="choose-radio-item__detail fl">银行卡、信用卡</span>
      <mt-radio class="fr" @change="payMethods" title="单选框列表" v-model="value" :options="['yin']"></mt-radio>
    </div> -->
  </div>
</template>

<script>
export default {
  data () {
    return {
      value: ''
    }
  },
  methods: {
    payMethods (val) {
      this.$emit('pay-method', val)
    }
  }
}
</script>

<style lang="less">
@import '../../assets/css/variable.less';

.choose-radio {
  background: @whiteColor;
  .choose-radio-item {
    height: 1.1rem;
    line-height: 1.1rem;
    margin: 0 .22rem;
    .pay-icon {
      width: .67rem;
      height: .51rem;
      margin-top: .3rem;
      background: url(../../assets/img/payconfirm/zhi@2x.png) no-repeat center center;
      background-size: .6rem .47rem;
    }
    &:nth-child(2) {
      .pay-icon {
        background: url(../../assets/img/payconfirm/wei@2x.png) no-repeat center center;
        background-size: .6rem .51rem;
      }
    }
    &:nth-child(3) {
      .pay-icon {
        background: url(../../assets/img/payconfirm/yin@2x.png) no-repeat center center;
        background-size: .67rem .42rem;
      }
    }
    .choose-radio-item__describe {
      font-size: .32rem;
      margin-left: .15rem;
    }
    .choose-radio-item__detail {
      font-size: .3rem;
      margin-left: 2.2rem;
      color: @lightColor;
    }
    .choose-radio-item__radio {
      width: .5rem;
    }
    .mint-radiolist {
      .mint-cell {
        min-height: 1rem;
      }
      .mint-radiolist-title {
        display: none;
      }
      .mint-radiolist-label {
        padding: 0;
      }
      .mint-radio-input:checked + .mint-radio-core {
        background: @activeColor;
        border-color: @activeColor;

      }
      .mint-radio-label {
        display: none;
      }
      .mint-cell-wrapper {
        height: 1rem;
        line-height: 1rem;
        padding: 0;
        .mint-radio-core {
          width: .3rem;
          height: .3rem;
          &::after {
            border: 2px solid @whiteColor;
            // background: @activeColor;
            background: transparent;
            transform: rotate(45deg) scale(1);
            border-radius: 0;
            border-left: 0;
            border-top: 0;
            top: 0;
            left: .07rem;
            width: .08rem;
            height: .16rem;
          }
        }
      }
    }
  }
}
</style>



