<template lang="html">
  <div class="need-pay clearfix">
    <span class="fl">需付款</span>
    <span class="fr">¥ {{cash.toFixed(2)}}</span>
  </div>
</template>

<script>
export default {
  props: {
    cash: {
      type: Number,
      default: 0
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../assets/css/variable.less';

.need-pay {
  padding: 0 .22rem;
  height: .88rem;
  line-height: .88rem;
  background: @whiteColor;
  span:first-child {
    color: @midleColor;
    font-size: .32rem;
  }
  span:last-child {
    color: @redColor;
    font-size: .3rem;
  }
}
</style>



