<template lang="html">
  <div class="pay-confirm-page">

    <!-- 头部 -->
    <default-head header="确认支付">  </default-head>

    <need-pay class="borderB" :cash="payInfo.amountDiscount"></need-pay>
    <!-- <div class="choose">
      <white-title borderB=".01rem solid #ddd" text="选择支付方式"></white-title>
    </div> -->
    <form action="#">
      <choose-radio @pay-method="getPayMethod"></choose-radio>
      <foot-btn text="立即支付" @click.native="toPayRightNow()"></foot-btn>
    </form>
  </div>
</template>

<script>
import api from '@/api'
import utils from '@/utils/utils'
import defaultHead from '@/components/head/defaultHead'
import needPay from '@/components/payConfirm/needPay'
import chooseRadio from '@/components/payConfirm/chooseRadio'
import whiteTitle from '@/components/common/whiteTitle'
import footBtn from '@/components/common/footBtn'

export default {
  data () {
    let {type, cash, disamount, payPwd, disamountId} = this.$route.query
    return {
      payInfo: {
        type: type,
        amount: parseFloat(cash),
        amountDiscount: parseFloat(disamount),
        payPassword: payPwd,
        discountId: parseFloat(disamountId)
      }
    }
  },
  computed: {
    userId () {
      return utils.userId()
    },
    userType () {
      return utils.getType()
    }
  },
  components: { defaultHead, needPay, chooseRadio, whiteTitle, footBtn },
  methods: {
    toPayRightNow () {
      let data = {}
      if (parseInt(this.payInfo.type) === 1) {
        data = {
          userId: this.userId,
          amount: this.payInfo.amountDiscount,
          payPassword: this.payInfo.payPassword,
          type: this.payInfo.type,
          userType: this.userType
        }
      } else {
        data = { ...this.payInfo, userType: this.userType, userId: this.userId }
      }
      this.webPageAlipayRecharge(data)
    },
    webPageAlipayRecharge (data) {
      api.webPageAlipayRecharge(data).then(response => {
        let result = response.body
        if (result.isSuccess) {
          let form = result.data
          document.write(form)
        }
      })
    },
    getPayMethod (val) {  // 选择支付方式回调
    }
  }
}
</script>

<style lang="less" scoped>
@import '../assets/css/variable.less';

.pay-confirm-page {
  padding-top: .88rem;
  .choose {
  }
}
</style>



